import { createToken, EmbeddedActionsParser, Lexer } from 'chevrotain';

// ----------------- Lexer -----------------

const OPEN = createToken({ name: 'OPEN', pattern: /(:?\(|%28)/ });
const CLOSE = createToken({ name: 'CLOSE', pattern: /(:?\)|%29)/ });
const COMMA = createToken({ name: 'COMMA', pattern: /(:?,|%2C)/ });
const SLASH = createToken({ name: 'SLASH', pattern: /\// });
const ANYALL = createToken({ name: 'COMMA', pattern: /(:?any|all)/ });
const COLON = createToken({ name: 'COLON', pattern: /(:?:|%3A)/ });
const SIMPLEIDENTIFIER = createToken({ name: 'SimpleIdentifier', pattern: /[a-zA-Z0-9_]{1,128}/ });
const SIMPLE_METHOD = createToken({
    name: 'SIMPLE_METHOD',
    pattern: /(:?length|tolower|toupper|trim|round|floor|ceiling)/
});
const COMPLEX_METHOD = createToken({
    name: 'COMPLEX_METHOD',
    pattern: /(:?concat|contains|endswith|indexof|matchesPattern|startswith|substring|cast)/
});
const TYPEDEF = createToken({ name: 'Typedef', pattern: /Edm\.[a-zA-Z]+/ });
// null, boolean, guid, dateTimeInOffset / dateValue / timeOfDay / decimalValue / doubleValue / singleValue / string / duration / enum / binary
const LITERAL = createToken({
    name: 'Literal',
    pattern: /(:?null|true|false|[0-9]{4}-[0-9]{2}-[0-9]{2}|-?(:?0|[1-9]\d*)(\.\d+)?(:?[eE][+-]?\d+)?|'[^\\"\n\r\']+')/
});
const LOGICAL_OPERATOR = createToken({ name: 'Logical', pattern: /(:?eq|ne|lt|le|gt|ge)/ });
const ANDOR = createToken({ name: 'AndOr', pattern: /(:?and|or)/ });
const WS = createToken({ name: 'Whitespace', pattern: /\s+/ });
const filterTokens = [
    OPEN,
    CLOSE,
    COMMA,
    COLON,
    SLASH,
    ANYALL,
    ANDOR,
    WS,
    SIMPLE_METHOD,
    COMPLEX_METHOD,
    LOGICAL_OPERATOR,
    TYPEDEF,
    LITERAL,
    SIMPLEIDENTIFIER
];

export const FilterLexer = new Lexer(filterTokens, {
    // Less position info tracked, reduces verbosity of the playground output.
    positionTracking: 'onlyStart'
});

// Parser
type CstRule<T> = (idxInCallingRule?: number, ...args: any[]) => T;
export class FilterParser extends EmbeddedActionsParser {
    filterExpr: CstRule<any>;
    expression: CstRule<any>;
    methodCallExpr: CstRule<any>;
    literalOrIdentifier: CstRule<any>;
    lambdaOperator: CstRule<any>;
    memberExpr: CstRule<any>;
    constructor() {
        super(filterTokens, {
            recoveryEnabled: true
        });

        // eslint-disable-next-line @typescript-eslint/no-this-alias
        const $ = this;

        $.filterExpr = $.RULE('filterExpr', () => {
            const expression = $.SUBRULE($.expression);
            return expression;
        });

        $.literalOrIdentifier = $.RULE('literalOrIdentifier', () => {
            const result = $.OR([
                {
                    ALT: () => $.CONSUME(TYPEDEF)
                },
                {
                    ALT: () => $.CONSUME(LITERAL)
                },
                {
                    ALT: () => $.SUBRULE($.memberExpr)
                }
            ]);
            return result;
        });
        $.methodCallExpr = $.RULE('methodCallExpr', () => {
            let methodNode;
            let literal1Node;
            let literal2Node;
            $.OR([
                {
                    ALT: () => {
                        methodNode = $.CONSUME(SIMPLE_METHOD);
                        $.CONSUME(OPEN);
                        literal1Node = $.SUBRULE($.literalOrIdentifier);
                        $.CONSUME(CLOSE);
                    }
                },
                {
                    ALT: () => {
                        methodNode = $.CONSUME(COMPLEX_METHOD);
                        $.OPTION(() => $.CONSUME(WS));
                        $.CONSUME2(OPEN);
                        $.OPTION2(() => $.CONSUME2(WS));
                        literal1Node = $.SUBRULE2($.literalOrIdentifier);
                        $.OPTION3(() => $.CONSUME3(WS));
                        $.CONSUME(COMMA);
                        $.OPTION4(() => $.CONSUME4(WS));
                        literal2Node = $.SUBRULE3($.literalOrIdentifier);
                        $.OPTION5(() => $.CONSUME5(WS));
                        $.CONSUME2(CLOSE);
                    }
                }
            ]);
            const methodArgs = [literal1Node.image ? literal1Node.image : literal1Node];
            if (literal2Node) {
                methodArgs.push(literal2Node.image ? literal2Node.image : literal2Node);
            }
            return {
                method: methodNode.image,
                methodArgs
            };
        });

        $.lambdaOperator = $.RULE('lambdaOperator', () => {
            const anyAll = $.CONSUME(ANYALL);
            $.CONSUME(OPEN);
            const key = $.CONSUME(SIMPLEIDENTIFIER);
            $.CONSUME(COLON);
            const subExpr = $.SUBRULE($.expression);
            $.OPTION(() => {
                $.CONSUME(CLOSE);
            });
            return {
                type: 'lambda',
                operator: anyAll.image.toUpperCase(),
                key: key.image,
                expression: subExpr.expressions && subExpr.expressions[0]
            };
        });

        $.memberExpr = $.RULE('memberExpr', () => {
            let memberDef = '';
            const identifier = $.CONSUME(SIMPLEIDENTIFIER);
            memberDef += identifier.image;
            $.OPTION(() => {
                $.CONSUME(SLASH);

                const outMember: any = $.OR([
                    {
                        ALT: () => $.SUBRULE($.memberExpr)
                    },
                    {
                        ALT: () => $.SUBRULE($.lambdaOperator)
                    }
                ]);
                if (outMember.expression) {
                    // Lamba
                    outMember.target = memberDef;
                    memberDef = outMember;
                } else {
                    memberDef += '/';
                    memberDef += outMember;
                }
            });
            return memberDef;
        });

        $.expression = $.RULE('expression', () => {
            const isExprOpen = $.OPTION1(() => $.CONSUME(OPEN));
            const identifier = $.OR([
                {
                    ALT: () => $.SUBRULE($.memberExpr)
                },
                {
                    ALT: () => $.SUBRULE($.methodCallExpr)
                }
            ]);

            let operator;
            let literal;
            let andOr;
            let subExpr;
            let currentOperator = '';
            let expressions = [];
            let isOpen;
            $.OPTION2(() => {
                $.CONSUME(WS);
                operator = $.CONSUME(LOGICAL_OPERATOR);
                $.CONSUME2(WS);
                literal = $.OR2([
                    {
                        ALT: () => $.CONSUME(LITERAL)
                    },
                    {
                        ALT: () => $.SUBRULE2($.methodCallExpr)
                    }
                ]);
                const expression = {
                    identifier,
                    operator: operator.image,
                    literal: literal.image ? literal.image : literal
                };
                expressions.push(expression);
                $.OPTION3(() => $.CONSUME(CLOSE));
                $.OPTION4(() => {
                    $.CONSUME5(WS);
                    andOr = $.CONSUME(ANDOR);
                    currentOperator = andOr.image.toUpperCase();

                    $.CONSUME6(WS);
                    isOpen = $.OPTION5(() => $.CONSUME2(OPEN));
                    subExpr = $.SUBRULE2($.expression);
                    if (currentOperator === subExpr.operator || subExpr.operator === '') {
                        expressions = expressions.concat(subExpr.expressions);
                    } else {
                        if (
                            (currentOperator === 'AND' && isOpen === undefined) ||
                            (isExprOpen !== undefined && isExprOpen.image !== undefined)
                        ) {
                            // AND has priority
                            expressions.push(subExpr.expressions.shift());
                            const subObject = {
                                expressions,
                                operator: currentOperator
                            };
                            expressions = [subObject].concat(subExpr.expressions);
                            currentOperator = subExpr.operator;
                        } else {
                            expressions.push(subExpr);
                        }
                    }
                    //currentOperator = subExpr.operator;

                    $.OPTION6(() => $.CONSUME2(CLOSE));
                });
                $.OPTION7(() => $.CONSUME3(CLOSE));
            });
            if (expressions.length === 0) {
                expressions.push({
                    identifier: identifier
                });
            }
            return {
                expressions,
                operator: currentOperator
            };
        });
        this.performSelfAnalysis();
    }
}

export function parseFilter(text) {
    const parser = new FilterParser();
    const lexingResult = FilterLexer.tokenize(text);
    // "input" is a setter which will reset the parser's state.
    parser.input = lexingResult.tokens;
    const output = parser.filterExpr();

    if (parser.errors.length > 0) {
        parser.errors.forEach(console.error);
        throw new Error('Parsing errors detected');
    }
    return output;
}
