import { Request, Response } from 'express';
import Router from 'router';
import { FileBasedServerConfig } from '../api';
import { loadFile } from './utils/fileLoader';

/**
 * @param config
 * @param version
 */
export function catalogService(config: FileBasedServerConfig, version = 2): Router {
    const router: Router = Router();

    router.get('/ServiceCollection', (req: Request, res: Response) => {
        res.json({
            d: {
                results: [
                    {
                        ID: config.service.name
                    }
                ]
            }
        });
    });

    router.get('/ServiceCollection\\(*', (req: Request, res: Response) => {
        res.json({
            d: {
                results: [
                    {
                        TechnicalName: config.service.name,
                        Version: version
                    }
                ]
            }
        });
    });

    if (config.annotations) {
        router.get('/Annotations*', (req: Request, res: Response) => {
            loadFile(config.annotations.localPath)
                .then((data) => {
                    res.setHeader('Content-Type', 'application/xml');
                    res.write(data);
                    res.end();
                })
                .catch((error) => {
                    console.error(error);
                });
        });
    }

    return router;
}
