export interface Key {
    COLUMN_NAME: string;
    LENGTH: number;
    DATA_TYPE_NAME: 'VARCHAR';
    IS_NULLABLE: 'FALSE';
}

/**
 * @param genKeyName
 */
export function createGenKeyProperty(genKeyName: string): Key {
    return {
        COLUMN_NAME: genKeyName,
        LENGTH: 2147483647,
        DATA_TYPE_NAME: 'VARCHAR',
        IS_NULLABLE: 'FALSE'
    };
}

/**
 * @param index
 */
export function createGenKeyValue(index: number): string {
    return '' + Date.now() + (index + 1);
}

/**
 * @param context
 */
export function createBoundary(context): string {
    return '' + Date.now() + '_' + context.getNextKeyCounter();
}
