import { EntityType, Property } from '@sap-ux/annotation-converter';
import { ODataMetadata } from './metadata';

// eslint-disable-next-line @typescript-eslint/no-empty-interface
export interface ODataRequestInterface {}
export interface EntitySetInterface {
    checkKeyValue(mockData: object, keyValues: object, keyName: string, keyProp?: Property): boolean;
    isV4(): boolean;
}
export interface DataAccessInterface {
    isV4(): boolean;
    getNavigationPropertyKeys(
        data: any,
        navPropDetail: any,
        currentEntityType: EntityType,
        currentKeys: Record<string, string>,
        forCreate?: boolean
    ): Record<string, string>;
    getMockEntitySet(
        entityTypeName,
        generateMockData?: boolean,
        containedEntityType?: EntityType,
        containedData?: any
    ): Promise<EntitySetInterface>;
    getData(odataRequest: ODataRequestInterface): Promise<any>;
    getMetadata(): ODataMetadata;
}

/**
 *
 */
export class ExecutionError extends Error {
    statusCode: number;
    messageData: object;
    isSAPMessage: boolean;
    isCustomError = true;

    /**
     * @param message
     * @param statusCode
     * @param messageData
     * @param isSAPMessage
     */
    constructor(message: string, statusCode: number, messageData: object, isSAPMessage: boolean) {
        super(message);
        this.statusCode = statusCode;
        this.messageData = messageData;
        this.isSAPMessage = isSAPMessage;
    }
}
