import { MockDataEntitySet } from './entitySet';
import { ODataRequest } from '../request/odataRequest';
import { Action, EntitySet, EntityType, Property, NavigationProperty } from '@sap-ux/annotation-converter';
import { FileBasedMockData, KeyDefinitions } from './fileBasedMockData';
import { uuidv4 } from './id';
import { DataAccessInterface } from './common';

type DraftElement = {
    IsActiveEntity: boolean;
    HasDraftEntity: boolean;
    HasActiveEntity: boolean;
    DraftAdministrativeData: DraftAdministrativeData;
};

type DraftAdministrativeData = {
    DraftUUID: string;
    CreationDateTime: string;
    CreatedByUser: string;
    DraftIsCreatedByMe: boolean;
    LastChangeDateTime: string;
    LastChangedByUser: string;
    InProcessByUser: string;
    DraftIsProcessedByMe: boolean;
};

/**
 * @param isV4
 */
function _getDateTimeOffset(isV4: boolean) {
    const date = new Date();
    date.setFullYear(2000 + Math.floor(Math.random() * 22));
    date.setDate(Math.floor(Math.random() * 30));
    date.setMonth(Math.floor(Math.random() * 12));
    date.setMilliseconds(0);
    return isV4 ? date.toISOString() : '/Date(' + date.getTime() + '+0000)/';
}

/**
 *
 */
export class DraftMockEntitySet extends MockDataEntitySet {
    /**
     * @param rootFolder
     * @param entitySetDefinition
     * @param dataAccess
     * @param generateMockData
     */
    constructor(
        rootFolder: string,
        entitySetDefinition: EntitySet | EntityType,
        dataAccess: DataAccessInterface,
        generateMockData: boolean
    ) {
        super(rootFolder, entitySetDefinition, dataAccess, generateMockData, true, true);
    }

    /**
     * @param filterExpression
     * @param mockData
     * @param allData
     */
    protected checkSpecificProperties(
        filterExpression: any,
        mockData: any,
        allData: FileBasedMockData
    ): boolean | null {
        if (filterExpression.identifier === 'DraftAdministrativeData/InProcessByUser') {
            return false;
        } else if (
            filterExpression.identifier === 'SiblingEntity/IsActiveEntity' &&
            filterExpression.literal === 'null'
        ) {
            // Ensure that there is not sibling entity which is inactive
            const keys = {};
            this.entityTypeDefinition.keys.forEach((keyDef) => {
                if (keyDef.name !== 'IsActiveEntity') {
                    keys[keyDef.name] = mockData[keyDef.name];
                } else {
                    keys[keyDef.name] = false;
                }
            });
            return !allData.hasEntry(keys);
        } else {
            return null;
        }
    }

    /**
     * @param mockData
     * @param keyValues
     * @param keyName
     * @param property
     */
    public checkKeyValue(mockData: object, keyValues: object, keyName: string, property?: Property): boolean {
        if (keyName === 'IsActiveEntity') {
            // Make sure we check a boolean value
            let booleanKeyValue = keyValues[keyName];
            if (typeof booleanKeyValue === 'string') {
                booleanKeyValue = booleanKeyValue === 'true';
            }
            return mockData[keyName] === booleanKeyValue;
        }
        return super.checkKeyValue(mockData, keyValues, keyName, property);
    }

    /**
     * @param keyValues
     * @param tenantId
     */
    public createInactiveVersion(keyValues: Record<string, string>, tenantId: string) {
        const currentMockData = this.getMockData(tenantId);
        const dataToDuplicate = this.performGET(keyValues, true, tenantId, true);
        dataToDuplicate.forEach((data) => {
            data.HasDraftEntity = true;
            const duplicate: DraftElement = Object.assign({}, data) as DraftElement;
            duplicate.IsActiveEntity = false;
            duplicate.HasActiveEntity = true;

            currentMockData.addEntry(duplicate);
        });
    }

    /**
     * @param keyValues
     * @param tenantId
     */
    public activateInactiveVersion(keyValues: Record<string, string>, tenantId: string) {
        const currentMockData = this.getMockData(tenantId);
        const dataToDuplicate = this.performGET(keyValues, true, tenantId, true);
        dataToDuplicate.forEach((draftData) => {
            const activateKeyValues = this.getKeys(draftData);
            activateKeyValues.IsActiveEntity = true;
            const activeDraft: DraftElement = Object.assign({}, draftData) as DraftElement;
            activeDraft.IsActiveEntity = true;
            activeDraft.HasDraftEntity = false;
            if (!currentMockData.hasEntry(activateKeyValues)) {
                currentMockData.addEntry(activeDraft);
            } else {
                currentMockData.updateEntry(activateKeyValues, activeDraft);
            }
        });
    }

    /**
     * @param keyValues
     * @param draftData
     * @param tenantId
     */
    public async draftDiscard(keyValues: Record<string, any>, draftData: object, tenantId: string) {
        super.performDELETE(keyValues, tenantId);
        for (const navPropName in this.entitySetDefinition.navigationPropertyBinding) {
            if (this.entitySetDefinition.navigationPropertyBinding[navPropName].annotations?.Common?.DraftNode) {
                // For all the draft node data duplicate them
                const navPropDetail = this.entityTypeDefinition.navigationProperties.find(
                    (navProp) => navProp.name === navPropName
                ) as NavigationProperty;
                const subKeys = this.dataAccess.getNavigationPropertyKeys(
                    draftData,
                    navPropDetail,
                    this.entitySetDefinition.entityType,
                    {}
                );
                const navPropEntity = (await this.dataAccess.getMockEntitySet(
                    this.entitySetDefinition.navigationPropertyBinding[navPropName].name
                )) as DraftMockEntitySet;
                if (navPropEntity && navPropEntity.discardInactiveVersion) {
                    navPropEntity.discardInactiveVersion(subKeys, tenantId);
                }
            }
        }
        const deleteKeyValues = Object.assign({}, keyValues);
        deleteKeyValues.IsActiveEntity = true;
        const activeData = this.performGET(deleteKeyValues, false, tenantId, true) as DraftElement;
        if (activeData) {
            activeData.HasDraftEntity = false;
        }
        return activeData;
    }

    /**
     * @param keyValues
     * @param tenantId
     */
    public discardInactiveVersion(keyValues: Record<string, string>, tenantId: string) {
        const dataToDiscard = this.performGET(keyValues, true, tenantId);
        dataToDiscard.forEach((data) => {
            const keys = this.getKeys(data);
            this.performDELETE(keys, tenantId);
            const deleteKeyValues = Object.assign({}, keys);
            deleteKeyValues.IsActiveEntity = true;
            const activeData = this.performGET(deleteKeyValues, false, tenantId, true) as DraftElement;
            if (activeData) {
                activeData.HasDraftEntity = false;
            }
        });
    }

    /**
     * @param actionDefinition
     * @param actionData
     * @param odataRequest
     * @param keys
     */
    public async executeAction(
        actionDefinition: Action,
        actionData: object,
        odataRequest: ODataRequest,
        keys: Record<string, any>
    ): Promise<any> {
        const currentMockData = this.getMockData(odataRequest.tenantId);
        actionData = currentMockData.onBeforeAction(actionDefinition, actionData, keys);
        let responseObject;
        switch (actionDefinition.fullyQualifiedName) {
            // Draft Edit Action
            case `${this.entitySetDefinition.annotations.Common.DraftRoot.EditAction}(${this.entitySetDefinition.entityTypeName})`:
            case `${this.entitySetDefinition.annotations.Common.DraftRoot.EditAction}()`: {
                const data = this.performGET(keys, false, odataRequest.tenantId, true) as DraftElement;

                data.HasDraftEntity = true;
                const duplicate: DraftElement = Object.assign({}, data) as DraftElement;
                duplicate.IsActiveEntity = false;
                duplicate.HasActiveEntity = true;
                const currentDate = _getDateTimeOffset(this.isV4());
                duplicate.DraftAdministrativeData = {
                    DraftUUID: uuidv4(),
                    CreationDateTime: currentDate,
                    CreatedByUser: 'nobody',
                    DraftIsCreatedByMe: true,
                    LastChangeDateTime: currentDate,
                    LastChangedByUser: 'nobody',
                    InProcessByUser: 'nobody',
                    DraftIsProcessedByMe: true
                };
                currentMockData.addEntry(duplicate);
                for (const navPropName in this.entitySetDefinition.navigationPropertyBinding) {
                    if (
                        this.entitySetDefinition.navigationPropertyBinding[navPropName].annotations?.Common?.DraftNode
                    ) {
                        // For all the draft node data duplicate them
                        const navPropDetail = this.entityTypeDefinition.navigationProperties.find(
                            (navProp) => navProp.name === navPropName
                        ) as NavigationProperty;
                        const subKeys = this.dataAccess.getNavigationPropertyKeys(
                            data,
                            navPropDetail,
                            this.entitySetDefinition.entityType,
                            {}
                        );
                        const navPropEntity = (await this.dataAccess.getMockEntitySet(
                            this.entitySetDefinition.navigationPropertyBinding[navPropName].name
                        )) as DraftMockEntitySet;
                        if (navPropEntity && navPropEntity.createInactiveVersion) {
                            navPropEntity.createInactiveVersion(subKeys, odataRequest.tenantId);
                        }
                    }
                }
                // responseObject = duplicate;
                odataRequest.query.queryPath.pop();
                const inactiveKeys = Object.assign({}, keys, { IsActiveEntity: false });
                odataRequest.query.queryPath[odataRequest.query.queryPath.length - 1].keys = inactiveKeys;
                responseObject = this.dataAccess.getData(odataRequest);
                break;
            }
            case `${this.entitySetDefinition.annotations.Common.DraftRoot.PreparationAction}(${this.entitySetDefinition.entityTypeName})`:
            case `${this.entitySetDefinition.annotations.Common.DraftRoot.PreparationAction}()`:
                // Prepare
                responseObject = this.performGET(keys, false, odataRequest.tenantId);
                break;

            case `${this.entitySetDefinition.annotations.Common.DraftRoot.DiscardAction}(${this.entitySetDefinition.entityTypeName})`:
            case `${this.entitySetDefinition.annotations.Common.DraftRoot.DiscardAction}()`: {
                // Discard
                const data = this.performGET(keys, false, odataRequest.tenantId) as DraftElement;
                const activeData = await this.draftDiscard(keys, data, odataRequest.tenantId);
                responseObject = activeData;
                break;
            }

            case `${this.entitySetDefinition.annotations.Common.DraftRoot.ActivationAction}(${this.entitySetDefinition.entityTypeName})`:
            case `${this.entitySetDefinition.annotations.Common.DraftRoot.ActivationAction}()`: {
                const draftData = this.performGET(keys, false, odataRequest.tenantId);

                const keyValues = Object.assign({}, keys);
                keyValues.IsActiveEntity = true;
                const activeDraft: DraftElement = Object.assign({}, draftData) as DraftElement;
                activeDraft.IsActiveEntity = true;
                activeDraft.DraftAdministrativeData = null;
                activeDraft.HasDraftEntity = false;
                if (currentMockData.hasEntry(keyValues)) {
                    currentMockData.updateEntry(keyValues, activeDraft);
                } else {
                    currentMockData.addEntry(activeDraft);
                }

                for (const navPropName in this.entitySetDefinition.navigationPropertyBinding) {
                    if (
                        this.entitySetDefinition.navigationPropertyBinding[navPropName].annotations?.Common?.DraftNode
                    ) {
                        // For all the draft node data duplicate them
                        const navPropDetail = this.entityTypeDefinition.navigationProperties.find(
                            (navProp) => navProp.name === navPropName
                        ) as NavigationProperty;
                        const subKeys = this.dataAccess.getNavigationPropertyKeys(
                            draftData,
                            navPropDetail,
                            this.entitySetDefinition.entityType,
                            {}
                        );
                        const navPropEntity = (await this.dataAccess.getMockEntitySet(
                            this.entitySetDefinition.navigationPropertyBinding[navPropName].name
                        )) as DraftMockEntitySet;
                        if (navPropEntity && navPropEntity.activateInactiveVersion) {
                            navPropEntity.activateInactiveVersion(subKeys, odataRequest.tenantId);
                        }
                    }
                }
                await this.draftDiscard(keys, draftData, odataRequest.tenantId);
                responseObject = activeDraft;
                break;
            }
            default:
                responseObject = currentMockData.executeAction(actionDefinition, actionData, keys);
                break;
        }
        responseObject = currentMockData.onAfterAction(actionDefinition, actionData, keys, responseObject);
        return responseObject;
    }

    /**
     * @param keyValues
     * @param postData
     * @param tenantId
     */
    public performPOST(keyValues: KeyDefinitions, postData: any, tenantId: string): any {
        // Validate potentially missing keys
        if (!Object.hasOwnProperty.call(postData, 'IsActiveEntity')) {
            postData.IsActiveEntity = false;
        }
        if (!Object.hasOwnProperty.call(postData, 'HasActiveEntity')) {
            postData.HasActiveEntity = false;
        }
        return super.performPOST(keyValues, postData, tenantId);
    }

    /**
     * @param keyValues
     * @param tenantId
     */
    public async performDELETE(keyValues: KeyDefinitions, tenantId: string): Promise<void> {
        const draftData = this.performGET(keyValues, false, tenantId);
        if (this.entitySetDefinition.annotations?.Common?.DraftRoot && draftData && !draftData.IsActiveEntity) {
            await this.draftDiscard(keyValues, draftData, tenantId);
        } else {
            return super.performDELETE(keyValues, tenantId);
        }
    }
}
