import { Request, Response } from 'express';
import Router from 'router';
import { getLogger } from '@ui5/logger';
import { oDataHandler } from './router/odata';
import { FolderBasedServerConfig, ServerConfig } from './api';
import { catalogService } from './router/catalog';
import * as fs from 'fs';
import path from 'path';
import url from 'url';

import { isInBrowser } from './router/utils/fileLoader';

/**
 * @param req
 * @param res
 * @param next
 */
function disableCache(req, res, next): void {
    res.setHeader('Cache-Control', 'private, no-cache, no-store, must-revalidate');
    res.setHeader('Expires', '-1');
    res.setHeader('Pragma', 'no-cache');
    next();
}

/**
 * @param str
 */
function encode(str) {
    return str.replace(/'/g, '%27');
}

/**
 * @param serverConfig
 */
function isFolderBasedConfig(serverConfig: ServerConfig): serverConfig is FolderBasedServerConfig {
    return (serverConfig as FolderBasedServerConfig).mockFolder !== undefined;
}

/**
 * @param config
 */
export async function createMockMiddleware(config: ServerConfig): Promise<Router> {
    const app: Router = Router();
    const log = getLogger('server:ux-fe-mockserver');
    app.use((req: Request, res: Response, next) => {
        next();
    });
    app.use(disableCache);
    if (config.watch) {
        log.info(`Mockserver is running in watch mode`);
    }

    if (isFolderBasedConfig(config)) {
        let mockConfig;
        if (fs.existsSync(path.join(config.mockFolder, 'config.js'))) {
            mockConfig = require(/* webpackIgnore: true */ path.join(config.mockFolder, 'config.js'));
        } else {
            mockConfig = JSON.parse(fs.readFileSync(path.join(config.mockFolder, 'config.json')).toString('utf-8'));
        }
        await Promise.all(
            mockConfig.map(async (mockConfig) => {
                const subConfig: ServerConfig = {
                    service: mockConfig
                };
                if (subConfig.service.metadataXmlPath) {
                    subConfig.service.metadataXmlPath = path.resolve(
                        config.mockFolder,
                        subConfig.service.metadataXmlPath
                    );
                }
                if (subConfig.service.metadataCdsPath) {
                    subConfig.service.metadataCdsPath = path.resolve(
                        config.mockFolder,
                        subConfig.service.metadataCdsPath
                    );
                }

                subConfig.service.mockdataRootPath = path.resolve(
                    config.mockFolder,
                    subConfig.service.mockdataRootPath
                );
                if (!subConfig.service.urlBasePath) {
                    subConfig.service.urlBasePath = '';
                }
                try {
                    const oDataHandlerInstance = await oDataHandler(subConfig);
                    if (config.contextBasedIsolation) {
                        const subRouter: Router = Router();
                        subRouter.use(
                            `${subConfig.service.urlBasePath}/${subConfig.service.name}`,
                            oDataHandlerInstance
                        );
                        subRouter.use(
                            `${subConfig.service.urlBasePath}/${encode(subConfig.service.name)}`,
                            oDataHandlerInstance
                        );
                        app.use(/^\/tenant-(\d{1,3})/, subRouter);
                    }
                    if (config.debug) {
                        log.info(`Mockdata location: ${subConfig.service.mockdataRootPath}`);
                        log.info(`Service path: ${subConfig.service.urlBasePath}/${subConfig.service.name}`);
                    }
                    app.use(`${subConfig.service.urlBasePath}/${subConfig.service.name}`, oDataHandlerInstance);
                    app.use(`${subConfig.service.urlBasePath}/${encode(subConfig.service.name)}`, oDataHandlerInstance);

                    app.use('/sap/opu/odata/IWFND/CATALOGSERVICE;v=2', catalogService(subConfig));
                } catch (e) {
                    if (config.debug) {
                        console.error(e);
                    }
                    console.log('Failed to start ' + subConfig.service.urlBasePath + '/' + subConfig.service.name);
                }
            })
        );
    } else {
        if (!config.service.urlBasePath) {
            config.service.urlBasePath = '';
        }
        log.info(`Mockdata location: ${config.service.mockdataRootPath}`);
        log.info(`Service path: ${config.service.urlBasePath}/${config.service.name}`);

        // eslint-disable-next-line prefer-const
        let serverInit;
        if (isInBrowser()) {
            // eslint-disable-next-line @typescript-eslint/no-var-requires
            const nise = require('nise');
            const fakeServer = nise.fakeServer;
            const escapePath = function (sPath) {
                // eslint-disable-next-line
                return sPath.replace(/[\\\/\[\]\{\}\(\)\-\*\+\?\.\^\$\|]/g, '\\$&');
            };

            nise.fakeXhr.FakeXMLHttpRequest.useFilters = true;

            const mockServer = fakeServer.create();
            mockServer.autoRespond = true;
            const serverhook = new RegExp(escapePath(config.service.urlBasePath) + '.*');
            nise.fakeXhr.FakeXMLHttpRequest.addFilter((method, url /*async, username, password*/) => {
                return !serverhook.test(url);
            });

            mockServer.respondWith(serverhook, async function (xhr /*, id*/) {
                const parsedUrl = url.parse(xhr.url);
                const req = {
                    url: parsedUrl.href,
                    method: xhr.method,
                    body: xhr.requestBody,
                    headers: xhr.requestHeaders
                };
                if (req.headers['Content-Type'] === 'multipart/mixed;charset=utf-8') {
                    const oBoundaryRegex = new RegExp('batch_[a-z0-9-]*');
                    const sBoundary = oBoundaryRegex.exec(req.body)[0];
                    req.headers['Content-Type'] = 'multipart/mixed; boundary=' + sBoundary;
                }
                Object.keys(req.headers).forEach((headerText) => {
                    req.headers[headerText.toLowerCase()] = req.headers[headerText];
                });
                const responseHeaders = {};
                let responseStatusCode = 200;
                let responseContentType;
                let responseBuffer = '';
                const res = {
                    header: (headerName, headerValue) => {
                        //responseHeaders[headerName] = headerValue;
                        responseHeaders[headerName.toLowerCase()] = headerValue;
                    },
                    getHeader: (headerName) => {
                        return responseHeaders[headerName];
                    },
                    setHeader: (headerName, headerValue) => {
                        res.header(headerName, headerValue);
                    },
                    status: (statusCode) => {
                        responseStatusCode = statusCode;
                    },
                    type: (contentType) => {
                        responseContentType = contentType;
                        return res;
                    },
                    contentType: (contentType) => {
                        responseContentType = contentType;
                        res.header('Content-Type', responseContentType);
                        return res;
                    },
                    write: (data: string) => {
                        responseBuffer += data;
                    },
                    end: () => {
                        res.send(responseBuffer);
                    },
                    send: (data: string) => {
                        xhr.readyState = 1;
                        if (typeof data === 'object') {
                            data = JSON.stringify(data);
                        }
                        xhr.respond(responseStatusCode, responseHeaders, data);
                    }
                };
                xhr.readyState = 4;
                await serverInit;
                app(req, res, function (arg1 /*arg2, arg3*/) {
                    console.error(arg1);
                });
            });
            app.mockServer = mockServer;
        }

        serverInit = oDataHandler(config).then((oDataHandlerInstance) => {
            if (config.contextBasedIsolation) {
                const subRouter: Router = Router();
                subRouter.use(`${config.service.urlBasePath}/${config.service.name}`, oDataHandlerInstance);
                subRouter.use(`${config.service.urlBasePath}/${encode(config.service.name)}`, oDataHandlerInstance);
                app.use(/^\/tenant-(\d{1,3})/, subRouter);
            }
            app.use(`${config.service.urlBasePath}/${config.service.name}`, oDataHandlerInstance);
            app.use(`${config.service.urlBasePath}/${encode(config.service.name)}`, oDataHandlerInstance);

            app.use('/sap/opu/odata/IWFND/CATALOGSERVICE;v=2', catalogService(config));
            return true;
        });
        app.readyPromise = serverInit;
    }

    return app;
}
