import { ODataRequest, DataQuery } from './odataRequest';
import { SimpleResponse } from '../batch/http/simpleHttpResponse';
import { ODataMetadata } from '../data/metadata';
import { parse } from 'query-string';
import balanced from 'balanced-match';

/**
 *
 */
export class ODataV4Request extends ODataRequest {
    public context: string;
    public dataLength: number;
    public location: string;
    /**
     * @param baseUrl
     * @param url
     * @param metadata
     */
    public constructor(baseUrl: string, url: string, metadata: ODataMetadata) {
        super(baseUrl, url, metadata);
        this.context = url.split('?')[0];
    }

    /**
     * @param testStr
     * @param char
     */
    countCharInString(testStr: string, char: string) {
        let charCount = 0;
        for (let i = 0; i < testStr.length; i++) {
            if (testStr[i] === char) {
                charCount++;
            }
        }
        return charCount;
    }
    /**
     * Comma separate list of properties that can contain sub-requests are split into an array.
     *
     * @param propertiesQuery OData properties request
     */
    protected splitProperties(propertiesQuery: string): string[] {
        const properties = [];
        let property = '',
            nestingDepth = 0;

        for (const char of propertiesQuery) {
            if (char === ',' && nestingDepth === 0) {
                // top-level delimiter - end of property
                if (property.length > 0) {
                    properties.push(property);
                }
                property = '';
            } else {
                property += char;
                if (char === '(') {
                    nestingDepth++;
                }
                if (char === ')') {
                    nestingDepth--;
                }
            }
        }
        if (nestingDepth === 0) {
            if (property.length > 0) {
                properties.push(property);
            }
        } else {
            throw `Invalid properties: ${propertiesQuery}`;
        }
        return properties;
    }

    /**
     * @param expandParameters
     */
    protected parseExpand(expandParameters: string): Partial<DataQuery> {
        const props = this.splitProperties(expandParameters);
        return props.reduce(
            (reducer, property) => {
                const match = property.match(/([^(]*)(\((.+)\))?/);
                const name = match[1];
                const parameters = match[3];
                const parameterSplit = parameters?.split(';');
                let queryPart = {};
                let previousParamData = '';
                parameterSplit?.forEach((paramData) => {
                    if (previousParamData.length > 0) {
                        previousParamData += ';';
                    }
                    previousParamData += paramData;
                    if (previousParamData.indexOf('(') !== -1) {
                        // In case we have an opening parenthesis we may not be balanced
                        const isBalanced = balanced('(', ')', previousParamData);
                        if (isBalanced) {
                            queryPart = Object.assign(queryPart, parse(previousParamData));
                            previousParamData = '';
                        }
                    } else {
                        queryPart = Object.assign(queryPart, parse(paramData));
                        previousParamData = '';
                    }
                });
                const expand = queryPart['$expand']
                    ? this.parseExpand(queryPart['$expand'])
                    : { expand: {}, properties: {} };
                const select = queryPart['$select'] ? queryPart['$select'].split(',') : [];
                const selectProperties = {};
                select.forEach((propName) => {
                    selectProperties[propName] = true;
                });
                Object.keys(expand.properties).forEach((expandName) => {
                    selectProperties[expandName] = true;
                });

                reducer.expand[name] = {
                    expand: expand.expand,
                    select: selectProperties
                };
                reducer.properties[name] = true;
                return reducer;
            },
            { expand: {}, properties: {} }
        );
    }

    /**
     * @param entity
     * @param element
     */
    protected enrichElement(entity: string, element: object): void {
        // nothing to do in v4
    }

    /**
     * @param data
     * @param bCreated
     * @param key
     */
    public createResponse(data: any, bCreated = false, key: string = undefined): SimpleResponse {
        const response = new SimpleResponse();
        if (data !== null) {
            response.write(this.toJSON(data));
            response.status(bCreated ? 204 : 200);
        } else {
            response.status(404);
        }
        response.setHeader('content-type', 'application/json;odata.metadata=minimal;IEEE754Compatible=true');
        response.setHeader('odata-version', '4.0');
        if (bCreated) {
            response.setHeader('location', this.location);
        }
        return response;
    }

    /**
     * @param data
     */
    protected toJSON(data: object | object[]): string {
        /*
        $select=ID,IsActiveEntity,category_ID,currency_code,identifier,price,rating,stock,thumbnailImage,title
        $expand=DraftAdministrativeData,category($select=ID,name),supplier($select=ID,city,name,phone,postCode,street)

        */
        // Header sap-contextid: SID:ANON:ldcicf6_CF6_00:PfxM66KUqPqnTLsyxAgp1J2aQJHbISjdfAQTi6e_-ATT
        // TODO: generate context based on this.uri
        //'@odata.context': `$metadata#${this.uri.entitySet}(${this.uri.query['$select']},${this.uri.query['$expand']})`
        let outContext = this.context;
        const dataLength = this.dataLength;
        if (outContext.indexOf('$metadata') === -1) {
            outContext = `$metadata#${this.context}`;
        }
        if (Array.isArray(data)) {
            const result = {
                '@odata.context': `${outContext}`,
                '@odata.count': dataLength,
                value: data
            };
            return JSON.stringify(result);
        } else {
            const result = {
                '@odata.context': `${outContext}`,
                '@odata.metadataEtag': 'W/"2DWIhBAR9jvPhquo53N+7tW+t3A5axeT11Xw3lkjcRY="'
            };
            return JSON.stringify(Object.assign(result, data));
        }
    }
}
